import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { api_routes } from "app/helpers/env-endpoints";

@Injectable({
  providedIn: "root"
})
export class DashboardService {
  private httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/json"
    })
  };

  constructor(private http: HttpClient) {}

  getAllCustomers(): Observable<any> {
    return this.http.get<any>(api_routes.GET_ALL_CUSTOMERS);
  }

  getAllVehicles(): Observable<any> {
    return this.http.get<any>(api_routes.GET_ALL_VEHICLES);
  }

  searchVehicles(customerId: any, status: any): Observable<any> {
    const requestUrl = "search/customer/" + customerId + "/status/" + status;
    console.log(api_routes.SEARCH + requestUrl);
    return this.http.get<any>(api_routes.SEARCH + requestUrl);
  }
}
